html {
  font-size: 62.5%;
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

body {
  font-family: "Sarabun", sans-serif;
  font-size: 1.6rem; }
  body.pantalla-fija {
    overflow: hidden; }

h1, h2, h3, h4 {
  margin: 0; }

h1 {
  font-size: 4rem; }

h2 {
  font-size: 3.8rem; }

h3 {
  font-size: 3rem; }

h4 {
  font-size: 2.5rem; }

p {
  font-size: 2rem;
  color: #0d0f11; }

a {
  text-decoration: none; }

.contenedor {
  max-width: 120rem;
  width: 95%;
  margin: 0 auto; }

img {
  max-width: 100%; }

.noMargin {
  margin: 0; }

.bg-verde {
  background-color: #5d9700; }

.bg-rojo {
  background-color: #d2374a; }

.bg-azul {
  background-color: #d7ecf4; }

.bg-morado {
  background-color: #6a1b9a; }

/*Header */
.header {
  background-color: #d2374a; }
  .header .contenidoHeader {
    display: flex;
    flex-direction: column;
    padding: 2.5rem 0;
    text-align: center; }
    @media (min-width: 768px) {
      .header .contenidoHeader {
        flex-direction: row;
        justify-content: space-between;
        align-items: center; } }
  .header .tituloHeader {
    color: #d7ecf4;
    margin-bottom: 2rem; }
    @media (min-width: 768px) {
      .header .tituloHeader {
        margin-bottom: 0; } }
  .header.fijo {
    position: fixed;
    width: 100%;
    box-shadow: 0  .3rem .5rem #444; }

/*Navegacion*/
.navegacionPrincipal {
  font-size: 2.5rem;
  display: flex;
  gap: 2.5rem;
  flex-direction: row;
  justify-content: center; }
  .navegacionPrincipal .navegacionElemento {
    color: #d7ecf4;
    font-weight: 300; }
    .navegacionPrincipal .navegacionElemento:hover {
      color: #0d0f11; }

.video {
  position: relative;
  max-height: 70rem;
  overflow: hidden; }
  .video .overlay {
    position: absolute;
    width: 100%;
    height: 100%;
    /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#e5e5e5+0,1d5410+50,1e5799+100&0.5+0,0.5+50,0.7+100,1+100 */
    background: -moz-linear-gradient(top, rgba(229, 229, 229, 0.5) 0%, rgba(29, 84, 16, 0.5) 50%, #1e5799 100%);
    /* FF3.6-15 */
    background: -webkit-linear-gradient(top, rgba(229, 229, 229, 0.5) 0%, rgba(29, 84, 16, 0.5) 50%, #1e5799 100%);
    /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(to bottom, rgba(229, 229, 229, 0.5) 0%, rgba(29, 84, 16, 0.5) 50%, #1e5799 100%);
    /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#80e5e5e5', endColorstr='#1e5799',GradientType=0 );
    /* IE6-9 */ }
  .video .videoPrincipal {
    display: block;
    width: 100%; }

.contenidoVideo {
  height: 100%;
  display: flex;
  align-items: center;
  position: relative;
  z-index: 1; }
  .contenidoVideo .tituloVideo {
    display: flex;
    flex-direction: column;
    font-size: 5rem;
    align-items: flex-start; }
    @media (min-width: 768px) {
      .contenidoVideo .tituloVideo {
        font-size: 7rem; } }
  .contenidoVideo .modificadorTitulo {
    background-color: #0d0f11;
    color: #d7ecf4;
    padding: .5rem 2rem;
    font-size: 2rem;
    font-weight: 400; }

.articulo {
  margin-top: 2rem;
  display: grid;
  grid-template-rows: repeat(2, 1fr); }
  @media (min-width: 768px) {
    .articulo {
      grid-template-rows: none;
      grid-template-columns: repeat(2, 1fr);
      gap: 2rem; } }
  @media (min-width: 1200px) {
    .articulo {
      gap: 5rem; } }

.contenidoArticulo {
  display: flex;
  flex-direction: column;
  justify-content: center; }
  .contenidoArticulo .fechaArticulo {
    color: #61000d;
    font-size: 2rem;
    margin-bottom: 0;
    margin: 2rem 0 2rem;
    font-weight: 700; }
    @media (min-width: 1200px) {
      .contenidoArticulo .fechaArticulo {
        margin-top: 5rem; } }
  .contenidoArticulo .textoArticulo {
    margin: 0;
    line-height: 1.5; }

.lineUp {
  margin-top: 2.5rem;
  background-color: #d7ecf4;
  padding: 5rem 0; }
  .lineUp .lineUpHeader {
    text-align: center;
    font-weight: 400; }
  .lineUp .dia {
    margin-top: 5rem;
    text-align: center;
    font-size: 3rem;
    text-transform: uppercase; }

.escenarios-contenedor {
  display: grid;
  gap: 5rem; }
  @media (min-width: 768px) {
    .escenarios-contenedor {
      grid-template-columns: repeat(2, 1fr); } }
  .escenarios-contenedor .nombre-escenario {
    margin-top: 5rem;
    text-align: center;
    font-weight: 300; }
    .escenarios-contenedor .nombre-escenario::after {
      content: '';
      width: 20rem;
      height: 10rem;
      display: block;
      margin: 2rem auto 0 auto;
      background-size: 20rem;
      background-repeat: no-repeat;
      background-position: center center; }

.edm .nombre-escenario::after {
  background-image: url(../img/icono_edm.png); }

.rock .nombre-escenario::after {
  background-image: url(../img/icono_rock.png); }

.calendario {
  list-style: none;
  padding: 0; }
  .calendario .elemento {
    font-size: 2rem;
    padding: 2.5rem;
    font-weight: 700; }
    .calendario .elemento .hora {
      font-weight: 300; }

.bg-rojo .calendario .elemento:nth-child(odd) {
  background-color: #851e2b; }

.bg-verde .calendario .elemento:nth-child(odd) {
  background-color: #3e6400; }

.galeria {
  margin-top: 5rem;
  text-align: center; }
  .galeria .galeria-imagenes {
    margin-top: 5rem;
    list-style: none;
    padding: 0;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem; }
    @media (min-width: 480px) {
      .galeria .galeria-imagenes {
        grid-template-columns: repeat(3, 1fr); } }
    @media (min-width: 768px) {
      .galeria .galeria-imagenes {
        grid-template-columns: repeat(4, 1fr); } }
    @media (min-width: 1200px) {
      .galeria .galeria-imagenes {
        grid-template-columns: repeat(6, 1fr); } }

.overlay {
  position: fixed;
  background-color: rgba(0, 0, 0, 0.8);
  width: 100%;
  height: 100%;
  top: 0;
  display: grid;
  place-items: center; }

.boleto {
  padding: 5rem; }
  .boleto .titulo-boleto {
    text-align: center;
    font-size: 3rem; }

.contenedor-boletos {
  display: grid; }
  @media (min-width: 768px) {
    .contenedor-boletos {
      grid-template-columns: repeat(2, 1fr); } }

.titulo-pases {
  font-size: 2.5rem;
  font-weight: 300;
  text-align: center; }

.pase {
  width: 95%;
  border-spacing: 0; }
  .pase .titulo-tabla th {
    padding: 2.5rem;
    color: #d7ecf4;
    font-size: 2.5rem;
    font-weight: 300;
    text-transform: uppercase; }
  .pase .cuerpo-tabla td {
    padding: 1rem;
    font-size: 2.2rem;
    border-bottom: 1px solid #e1e1e1; }
  .pase .cuerpo-tabla tr:last-of-type td {
    border-bottom: none; }
  .pase .pie-tabla td {
    padding: 1rem;
    font-size: 2.5rem;
    text-align: center;
    color: #d7ecf4; }
