
.lineUp{
    margin-top: $separacion/2;
    background-color: $azul;
    padding: $separacion 0;
    
    .lineUpHeader{
        text-align: center;
        font-weight: $regular;
    }
    .dia{
        margin-top: $separacion ;
        text-align: center;
        font-size: 3rem;
        text-transform: uppercase;
    }
}

.escenarios-contenedor{
    display: grid;
    gap:$separacion;
    @include tableta{
        grid-template-columns:repeat(2, 1fr);
    }
    
    .nombre-escenario{
        margin-top: $separacion;
        text-align: center;
        font-weight: $delgada;
        &::after{
            content:'';
            width: 20rem;
            height: 10rem;
            display: block;
            margin:2rem auto 0 auto;
            background-size: 20rem;
            background-repeat: no-repeat;
            background-position: center center;
        }
    }   
}

.edm .nombre-escenario{
    &::after{
        background-image: url(../img/icono_edm.png);
    }
}
.rock .nombre-escenario{
    &::after{
        background-image: url(../img/icono_rock.png);
    }
}

.calendario{
    list-style: none;
    padding: 0;
    .elemento{
        font-size: 2rem;
        padding:$separacion/2 ;
        font-weight: $bold;
        .hora{
            font-weight: $delgada;
        }
    }
}

.bg-rojo .calendario .elemento:nth-child(odd){
    background-color: darken($rosado, 20);
}
.bg-verde .calendario .elemento:nth-child(odd){
    background-color: darken($verde, 10);
}
