html{
    font-size: 62.5%;
    box-sizing: border-box;
}
*,*:before,*:after{
    box-sizing: inherit;
}
body{
    font-family: $fuenteTexto;
    font-size: 1.6rem;
    &.pantalla-fija{
        overflow: hidden;
    }
}
h1,h2,h3,h4{
    margin: 0;
}

h1{
    font-size: 4rem;
}

h2{
    font-size: 3.8rem;
}
h3{
    font-size: 3rem;
}
h4{
    font-size: 2.5rem;
}
p{
    font-size: 2rem;
    color: $negro;
}
a{
    text-decoration: none; 
}
.contenedor{
    max-width: 120rem;
    width: 95%;
    margin: 0 auto;
}

img{
    max-width: 100%;
}

.noMargin{
    margin: 0;
}
