.video{
    position: relative;
    max-height: 70rem;
    overflow: hidden;

    .overlay{
        position:absolute;
        width: 100%;
        height: 100%;
        /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#e5e5e5+0,1d5410+50,1e5799+100&0.5+0,0.5+50,0.7+100,1+100 */
        background: -moz-linear-gradient(top,  rgba(229,229,229,0.5) 0%, rgba(29,84,16,0.5) 50%, rgba(30,87,153,1) 100%); /* FF3.6-15 */
        background: -webkit-linear-gradient(top,  rgba(229,229,229,0.5) 0%,rgba(29,84,16,0.5) 50%,rgba(30,87,153,1) 100%); /* Chrome10-25,Safari5.1-6 */
        background: linear-gradient(to bottom,  rgba(229,229,229,0.5) 0%,rgba(29,84,16,0.5) 50%,rgba(30,87,153,1) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
        filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#80e5e5e5', endColorstr='#1e5799',GradientType=0 ); /* IE6-9 */
    }
    .videoPrincipal{
        display: block;
        width: 100%;  
        
    }
}
.contenidoVideo{
    height: 100%;
    display: flex;
    align-items: center;
    position: relative;
    z-index: 1;

    .tituloVideo{
        display: flex;
        flex-direction: column;
        font-size: 5rem;
        align-items: flex-start;
        @include tableta{
            font-size: 7rem;
        }
    }
    .modificadorTitulo{
        background-color: $negro;
        color: $azul;
        padding: .5rem 2rem;
        font-size: 2rem;
        font-weight: $regular;
       
    
        
    }
}