.articulo{
    margin-top: 2rem;
    display: grid;
    grid-template-rows: repeat(2, 1fr);

    @include tableta{
        grid-template-rows: none;
        grid-template-columns: repeat(2,1fr);
        gap: 2rem;
    }
    @include escritorio{
        gap: $separacion;
    }
     
}

.contenidoArticulo{
    display: flex;
    flex-direction: column;
    justify-content:center;   


    .fechaArticulo{
        color: $rojo;
        font-size: 2rem;
        margin-bottom: 0;
        margin: 2rem 0 2rem;
        font-weight: $bold;
        @include escritorio{
            margin-top: $separacion ;
        }
    }
    .textoArticulo{
        margin: 0;
        line-height: 1.5;
    } 
}
