.galeria{
    margin-top: $separacion;
    text-align: center;
    .galeria-imagenes{
        margin-top: $separacion;
        list-style: none;
        padding: 0;
        display: grid;
        grid-template-columns: repeat(2, 1fr); 
        gap:2rem;
        @include telefono{
            grid-template-columns: repeat(3, 1fr);
        }
        @include tableta{
            grid-template-columns: repeat(4, 1fr);
        }
        @include escritorio{
            grid-template-columns: repeat(6, 1fr);
        }
    } 
}
.overlay {
    position: fixed;
    background-color: rgba(0, 0, 0, .8);
    width: 100%;
    height: 100%;
    top: 0;
    display: grid;
    place-items: center;
}


