.boleto{
    padding: $separacion;
    .titulo-boleto{
        text-align: center;
        font-size: 3rem;
    }
}

.contenedor-boletos{
    display: grid;
    @include tableta{
        grid-template-columns: repeat(2,1fr);
        
    }
}
.titulo-pases{
    font-size: 2.5rem;
    font-weight: $delgada;
    text-align: center;
}

.pase{
    width: 95%;
    border-spacing: 0;
    .titulo-tabla{
        th{
            padding:$separacion /2 ;
            color: $azul;
            font-size: 2.5rem;
            font-weight:$delgada ;
            text-transform: uppercase;
        }
    }
    .cuerpo-tabla{
        td{
            padding: $separacion/5;
            font-size: 2.2rem;
            border-bottom: 1px solid #e1e1e1;
        }
        tr{
            &:last-of-type{
                td{
                    border-bottom: none;
                }
            }
        }
    

    }
    .pie-tabla{
        td{
            padding: $separacion/5;
            font-size: 2.5rem;
            text-align: center;
            color: $azul;
            
        }

    }
}