/*Header */
.header{
    background-color: $rosado;
    
    .contenidoHeader{
        display: flex;
        flex-direction: column;
        padding: $separacion/2 0;
        text-align: center;
        @include tableta {
            flex-direction: row;
            justify-content: space-between;
            align-items: center;
        }
    }
    .tituloHeader{
        color: $azul;
        margin-bottom: 2rem;
        @include tableta{
            margin-bottom: 0;
        }
    }

    &.fijo{
        position: fixed;
        width: 100%;
        box-shadow: 0  .3rem .5rem #444;
    }
}


/*Navegacion*/
.navegacionPrincipal{
    font-size: 2.5rem;
    display: flex;
    gap: $separacion/2;
    flex-direction: row;
    justify-content: center; 
    
    .navegacionElemento{
        color: $azul;
        font-weight: $delgada;
        &:hover{
            color: $negro;
        }
    }
}

    

